import React, { useState } from "react";
import ChecksumExample from "../components/ChecksumExample";
import ChecksumExercise from "../components/ChecksumExercise";
import MC from "../components/MC";
import YN from "../components/YN";
import randomDigits from "../functions/randomDigits";
import sumChecksum from "../functions/sumChecksum";
import errormaker from "../functions/errormaker";

/*
Task 1: Checksums

Subtask A: Given some examples of a checksum method,
calculate the checksum of other examples.

Subtask B: Given some number sequences and some checksums,
that can be wrong, the user must identify the wrong ones.

Subtask C: Some yes and no questions about the properties
of the given checksum.
*/

function Task1() {
  // number of examples displayed in the exercise
  const numOfExamples = 3;

  // number of wrong answers, before solution is revealed
  const minWrongAnswers = 3;

  // number of questions for subtasks to solve
  const numberOfTasksA = 3;
  const numberOfTasksB = 5;

  /* 
  Keeping track of number of correct solutions or wrong answers
  for subtask A. If correctAnswersA === numberOfTasks, then
  subtask A is solved and the next subtask is revealed.
  If wrongAnswersA === minWrongAnswers, then solution for subtask
  A is revealed.
  */
  const [wrongAnswersA, setWrongAnswersA] = useState(0);
  const [correctAnswersA, setCorrectAnswersA] = useState(0);

  // if taskStateB === true, then task C is revealed.
  const [taskStateB, setTaskStateB] = useState(false);

  // mc questions and answer keys for task B
  const [BTemp] = useState(
    Array.from({ length: numberOfTasksB }, () => {
      let seq1 = randomDigits();
      let cs1 = sumChecksum(seq1);
      errormaker(seq1, 0);
      let cs2 = sumChecksum(seq1);
      return [seq1.join("") + " " + cs1, cs1 !== cs2];
    })
  );

  let mcOptionsTaskB = [];
  let mcAKTaskB = [];

  for (let i = 0; i < numberOfTasksB; ++i) {
    let elemet = BTemp[i];
    mcOptionsTaskB[i] = elemet[0];
    mcAKTaskB[i] = elemet[1];
  }

  // if true, next task is revealed
  const [taskStateC, setTaskStateC] = useState(Array(5).fill(false));

  // revealing next part of task C
  const handleTaskStateC = (index) => {
    let temp = [...taskStateC];
    temp[index] = true;
    setTaskStateC(temp);
  };

  return (
    <div className="task">
      <h1>Aufgabe 1: Prfziffer und Fehlererkennung 1</h1>
      <div>
        <p>
          Eine Mglichkeit um Fehler in Daten zu erkennen, ist es, die Daten mit
          weiteren Prfziffern zu ergnzen. In diesem Beispiel haben wir einige
          Folgen von Zahlen gegeben (hier in Schwarz) und die dazugehrigen
          Prfziffern (hier in Rot). Die Prfziffer wird durch die Folge der
          Zahlen festgelegt. Knnen Sie in diesem Beispiel herausfinden, wie man
          aus der gegebenen Zahlenfolge die Prfziffer berechnen kann? Ergnzen
          Sie die weiteren Prfziffern der gegebenen Zahlenfolgen.
        </p>
        {[...Array(numOfExamples)].map((e, i) => (
          <ChecksumExample
            key={i}
            checksumFunction={sumChecksum}
            sequence={randomDigits}
          />
        ))}
        {[...Array(numberOfTasksA)].map((e, i) => (
          <ChecksumExercise
            key={i}
            checksumFunction={sumChecksum}
            sequence={randomDigits}
            onWorong={() => setWrongAnswersA(wrongAnswersA + 1)}
            onCorrect={() => setCorrectAnswersA(correctAnswersA + 1)}
          />
        ))}
        {wrongAnswersA >= minWrongAnswers &&
          correctAnswersA !== numberOfTasksA && (
            <p>
              Das ist leider nicht korrekt. Die Lsung der Aufgabe ist, dass die
              Prfziffer die Summe der einzelnen Zahlen der Zahlenfolge ist. Um
              fortfahren zu knnen, mssen Sie die Aufgabe korrekt lsen.
            </p>
          )}
      </div>
      {correctAnswersA === numberOfTasksA && (
        <div>
          <p>
            Beim bermitteln von Daten, oder in unserem Beispiel von Zahlen,
            kann es vorkommen, dass die eine oder andere Ziffer falsch ist. Der
            Fehler kann sowohl in der Zahlenfolge auftreten, oder aber auch in
            der Prfziffer selbst.
          </p>
          <MC
            question={
              "Gegeben sind weitere Beispiele von Zahlenfolgen und ihre Prfsummen. Bestimmen sie die Prfsummen, die falsch sind."
            }
            options={mcOptionsTaskB}
            answerKey={mcAKTaskB}
            textOnCorrect={
              "Die Summe der Zahlenfolge muss immer gleich der Prfziffer sein. Ansosten muss ein Fehler vorgefallen sein."
            }
            textOnWrong={
              "Die falschen Prfsummen sind die, welche nicht gleich der Summe der Zahlenfolge sind."
            }
            callerFunction={() => {
              setTaskStateB(true);
            }}
          />
        </div>
      )}
      {taskStateB && (
        <div>
          <p>
            Die Prfsumme ist eine einfache Methode, um zu berprfen, ob ein
            Fehler beim Versenden von Daten aufgetreten ist. Diese Methode hat
            einige Vor- und einige Nachteile. Belegen Sie, ob diese Aussagen
            korrekt oder falsch sind.
          </p>
          <YN
            question={
              "Wenn genau eine Ziffer in den Daten falsch ist, dann erkennt man das an der Prfziffer."
            }
            solution={1}
            optionYes={"Ja"}
            optionNo={"Nein"}
            textOnCorrect={
              "Wenn eine Ziffer falsch ist, dann stimmt die Summe nicht mehr mit der Prfziffer berein."
            }
            textOnWrong={
              "Wenn eine Ziffer falsch ist, dann stimmt die Summe nicht mehr mit der Prfziffer berein."
            }
            callerFunction={() => handleTaskStateC(0)}
          />
          {taskStateC[0] && (
            <YN
              question={
                "Wenn genau eine Ziffer in der Prfziffer falsch ist, dann sind auch die Daten falsch."
              }
              solution={0}
              optionYes={"Ja"}
              optionNo={"Nein"}
              textOnCorrect={
                "Der Fehler kann in der Prfsumme selber vorkommen, auch wenn die Daten korrekt sind. Man muss zwar davon ausgehen, dass ein Fehler vorgefallen ist und die Daten neu senden."
              }
              textOnWrong={
                "Die Daten knnen korrekt sein, aber die Prfsumme selber knnte einen Fehler beinhalten. Man muss zwar davon ausgehen, dass ein Fehler vorgefallen ist."
              }
              callerFunction={() => handleTaskStateC(1)}
            />
          )}
          {taskStateC[1] && (
            <YN
              question={
                "Ein hufiger Fehler ist, dass man die Ziffern in der falschen Reihenfolge aufschreib, z.B. 73 statt 37. Kann die Prfsumme solche Fehler erkennen?"
              }
              solution={0}
              optionYes={"Ja"}
              optionNo={"Nein"}
              textOnCorrect={
                "Die Prfsumme ist die Summe der einzelnen Zahlen es spielt keine Rolle, in welcher Reihenfolge man die Zahlen addiert (7 + 3 = 3 + 7)."
              }
              textOnWrong={
                "Die Prfsumme ist die Summe der einzelnen Zahlen es spielt keine Rolle, in welcher Reihenfolge man die Zahlen addiert (7 + 3 = 3 + 7)."
              }
              callerFunction={() => handleTaskStateC(2)}
            />
          )}
          {taskStateC[2] && (
            <YN
              question={
                "Wenn genau eine Ziffer in den Daten falsch ist, kann man mit der Prfsumme erkennen, welche Ziffer das ist."
              }
              solution={0}
              optionYes={"Ja"}
              optionNo={"Nein"}
              textOnCorrect={
                "Man erkennt nur, dass ein Fehler passiert ist, aber nicht wo genau. Man kann mit der Prfsumme nur Fehler erkennen, aber nicht korrigieren."
              }
              textOnWrong={
                "Angenommen die Zahlenfolge wre 234 und die Prfsumme 7. Dann kann es sein, dass die korrekte Zahlenfolge 231 gewesen wre, oder aber auch 214. Man kann mit der Prfsumme nur Fehler erkennen, aber nicht korrigieren."
              }
              callerFunction={() => handleTaskStateC(3)}
            />
          )}
          {taskStateC[3] && (
            <YN
              question={
                "Erkennt man mit der Prfsumme, ob die Daten falsch sind, wenn genau zwei Ziffern falsch sind?"
              }
              solution={0}
              optionYes={"Ja"}
              optionNo={"Nein"}
              textOnCorrect={
                "Eine Ziffer knnte um den Betrag x hher sein und eine andere Ziffer um den gleichen Betrag x tiefer. Die Summe bleibt dennoch gleich."
              }
              textOnWrong={
                "Angenommen die Zahlenfolge wre 234 und die Prfsumme 9. Dann kann es sein, dass die korrekte Zahlenfolge 216 gewesen wre, oder aber auch 531."
              }
              callerFunction={() => handleTaskStateC(4)}
            />
          )}
          {taskStateC[4] && (
            <YN
              question={
                "Erkennt man den Fehler, wenn man eine Ziffer in der Zahlenfolge vergisst oder auslsst?"
              }
              solution={0}
              optionYes={"Ja"}
              optionNo={"Nein"}
              textOnCorrect={
                "Die Ziffer 0 knnte weggelassen werden und die Summe bleibt gleich. Generell sagt die Prfsumme nichts aus ber die Anzahl Nullen in der Zahlenfolge. 1200000 hat die gleiche Prfsumme wie 120."
              }
              textOnWrong={
                "Die Ziffer 0 knnte weggelassen werden und die Summe bleibt gleich. Generell sagt die Prfsumme nichts aus ber die Anzahl Nullen in der Zahlenfolge. 1200000 hat die gleiche Prfsumme wie 120. Wenn man 0 nicht erlaubt, dann htten Sie recht."
              }
              callerFunction={() => {}}
            />
          )}
        </div>
      )}
    </div>
  );
}

export default Task1;
